html {
  height: 100%;
  overflow: auto; }

body {
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: "Fjalla One", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.body-text {
  font-family: "Average", sans-serif; }

a {
  text-decoration: none; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

button {
  box-shadow: none; }

div[class^="StyledBox__StyledBoxGap"] {
  display: none; }
